import "highlight.js/styles/agate.css";
import ReactMarkdown from 'react-markdown';     //   (Ract  or Html  )
import remarkGfm from 'remark-gfm';             // , ,      (react-markdown     )
import rehypeHighlight from "rehype-highlight"; //  code   (highlight.js/styles/a11y-dark.css  import  )

interface WorkProps {
    propsMarkdown: string[];
}

const Work : React.FC<WorkProps> = ({ propsMarkdown }) => {
    // const listArray = propsMarkdown.map((markdown, index)=> markdown);
    return (
        <div className="work mark-class">
            {/* ReactMarkdown  markdown   */}
            {propsMarkdown.map((markdown, index)=>(
                <ReactMarkdown key={index} remarkPlugins={[remarkGfm]} rehypePlugins={[rehypeHighlight]}>
                    {markdown}
                </ReactMarkdown>
            ))}
        </div>
    );
};

export default Work;